
.maindivnav{
  display: flex;
  justify-content: space-between;

  align-items: center;
  padding: 1rem;
  position: sticky;
  top: 0px;
  z-index: 10;
  background-color: black;
  
 
  
}


 .nav-menu{
  display: flex;
 
 
 }

.nav-menu li{
    
   padding:0 1rem;
}
.nav-menu li a {
  font-size: 1.4rem;
  font-weight: 500;
}

.hamburger{
  display: none;
}
@media screen and (max-width:1040px){

  .nav-menu{
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    background: right(0,0,0,0,0.9);
    position: absolute;
    top: 0;
     left: -100%;
    z-index: -3;
   

  }

  .nav-menu .active{
    left: 0;
  
  }

  .nav-menu li{
    padding:1rem 0 ;
  }

  .nav-menu li a{
    font-size: 1.5rem ;
  }

  .hamburger{
    display:flex;
    flex-direction: row-reverse;
    
  }
  .maindivnav{
    background-color: white;
    
   
  }
  
  
}


